"use client";

import { useEffect, useRef } from "react";
import * as THREE from "three";
import Stats from "three/examples/jsm/libs/stats.module";
import gsap from "gsap";

export default function HomePage() {
  const containerRef = useRef(null);
  const rendererRef = useRef(null);
  const cameraRef = useRef(null);
  const sceneRef = useRef(null);
  const planeGroupRef = useRef(null);
  const raycasterRef = useRef(new THREE.Raycaster());
  const mouse = useRef({ x: 0, y: 0 });
  const targetRotation = useRef(0);
  const planes = useRef([]);

  useEffect(() => {
    const container = containerRef.current;
    const width = container.offsetWidth;
    const height = container.offsetHeight;

    // Scene & Camera
    const scene = new THREE.Scene();
    sceneRef.current = scene;

    const camera = new THREE.PerspectiveCamera(60, width / height, 0.1, 1000);
    camera.position.set(0, 0, 7);
    cameraRef.current = camera;

    // Renderer
    const renderer = new THREE.WebGLRenderer({ alpha: true, antialias: true });
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2));
    renderer.setSize(width, height);
    renderer.setClearColor(0x000000, 0);
    container.appendChild(renderer.domElement);
    rendererRef.current = renderer;

    // Stats
    const stats = new Stats();
    stats.dom.style.position = "absolute";
    stats.dom.style.top = 0;
    document.body.appendChild(stats.dom);

    // Planes
    const textureLoader = new THREE.TextureLoader();
    const radius = 4;
    const numPlanes = 12;
    const angleIncrement = (2 * Math.PI) / numPlanes;

    const group = new THREE.Group();
    planeGroupRef.current = group;

    for (let i = 0; i < numPlanes; i++) {
      const texture = textureLoader.load(
        `https://picsum.photos/1280/720.webp?random=${i + 1}`
      );
      const geometry = new THREE.PlaneGeometry(1.6, 0.9);
      const material = new THREE.MeshBasicMaterial({
        map: texture,
        side: THREE.DoubleSide,
        transparent: true,
      });
      const plane = new THREE.Mesh(geometry, material);

      const angle = i * angleIncrement;
      plane.position.set(Math.cos(angle) * radius, 0, Math.sin(angle) * radius);
      plane.lookAt(0, 0, 0);

      group.add(plane);
      planes.current.push(plane);
    }

    scene.add(group);

    // Mouse move event
    const onMouseMove = (e) => {
      mouse.current.x = (e.clientX / width) * 2 - 1;
      mouse.current.y = -(e.clientY / height) * 2 + 1;
    };
    window.addEventListener("mousemove", onMouseMove);

    // Mouse wheel scroll
    const onWheel = (event) => {
      const delta = event.deltaY * 0.005;
      targetRotation.current += delta;
      gsap.to(group.rotation, {
        y: targetRotation.current,
        duration: 1,
        ease: "power1.out",
      });
    };
    window.addEventListener("wheel", onWheel, { passive: false });

    // Disable default scroll
    window.addEventListener(
      "wheel",
      (e) => {
        e.preventDefault();
        e.stopPropagation();
        return false;
      },
      { passive: false }
    );

    // Click to rotate camera
    const onClick = () => {
      raycasterRef.current.setFromCamera(
        new THREE.Vector2(mouse.current.x, mouse.current.y),
        camera
      );
      const intersects = raycasterRef.current.intersectObjects(group.children);

      if (intersects.length > 0) {
        const clicked = intersects[0].object;
        const pos = new THREE.Vector3();
        clicked.getWorldPosition(pos);
        const angle = Math.atan2(pos.z, pos.x);

        gsap.to(camera.position, {
          x: Math.cos(angle) * camera.position.length(),
          z: Math.sin(angle) * camera.position.length(),
          duration: 1,
          onUpdate: () => {
            camera.lookAt(scene.position);
          },
        });
      }
    };
    window.addEventListener("click", onClick);

    // Resize
    const onResize = () => {
      const newWidth = container.offsetWidth;
      const newHeight = container.offsetHeight;
      camera.aspect = newWidth / newHeight;
      camera.updateProjectionMatrix();
      renderer.setSize(newWidth, newHeight);
    };
    window.addEventListener("resize", onResize);

    // Render loop
    const render = () => {
      requestAnimationFrame(render);

      // Tilt based on mouse
      group.rotation.x = mouse.current.y * 0.1;
      group.rotation.z = -mouse.current.x * 0.1;

      // Raycast hover
      raycasterRef.current.setFromCamera(
        new THREE.Vector2(mouse.current.x, mouse.current.y),
        camera
      );
      const intersects = raycasterRef.current.intersectObjects(group.children);

      if (intersects.length > 0) {
        document.body.classList.add("hovered");
        const hovered = intersects[0].object;
        planes.current.forEach((plane) => {
          if (plane === hovered) {
            gsap.to(plane.material, { opacity: 1, duration: 0.5 });
          } else {
            gsap.to(plane.material, { opacity: 0.5, duration: 0.5 });
          }
        });
      } else {
        document.body.classList.remove("hovered");
        planes.current.forEach((plane) => {
          gsap.to(plane.material, { opacity: 1, duration: 0.5 });
        });
      }

      renderer.render(scene, camera);
      stats.update();
    };
    render();

    // Cleanup
    return () => {
      window.removeEventListener("mousemove", onMouseMove);
      window.removeEventListener("wheel", onWheel);
      window.removeEventListener("resize", onResize);
      window.removeEventListener("click", onClick);
      stats.dom.remove();
      renderer.dispose();
    };
  }, []);

  return <div ref={containerRef} style={{ width: "100vw", height: "100vh" }} />;
}
